.shoptop{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: auto;
    height: auto;
    padding: 4rem;
 

  
}
.upperfotterr{
    background-color: rgb(219, 233, 233);
  
}

.shoptop>h2{
    
    margin-bottom: 2rem;

}

.shoptop>p{
    width: 70%;
   color: grey;

}

.shoptop2{
    align-items: center;
    justify-content: space-around;
   
}
.shoptopimg{
    display: flex;
    justify-content: space-between;
    width: 80%;
 
    margin: auto;
    background-color: rgba(214, 227, 227, 0.936);
}
.imgsrcc{
    width: 100%;
}
.cardsec{
    width: 50%;
   margin:5px;
   
    background-color: rgba(240, 248, 255, 0.832);
}
.imgdata{
    margin: 3%;
 padding: 5%;
 line-height: 1.5;
}




@media (max-width: 720px) {
    .shoptopimg{
        display: block;
        width: 95%;
    }
    .cardsec{
        width: 80%;
       margin: 1.5rem;
       
    }
  }