*{
    padding: 0;
    margin: 0;
    box-sizing: border-box; 
    
}



.upperfottercontent{
    
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: auto;
    height: auto;
    padding: 4rem;
   
    
}

.upperfottercontent>h2{
    margin-bottom: 2rem;
    

}

.upperfotterimage{
    max-width: 100%;
    height: auto;
    object-fit: cover;
margin: auto;

}

.upperfotterimage>img{

   width: 16.6%;

}


