




.footer {
    display: flex;
    background-color: black;
    color: white;
    padding: 80px;
    height:auto;
  }
  
  .footer-logo-section {
    flex-basis: 30%;
  }
  
  .footerlogo {
    padding-right: 20px;
    cursor: pointer;
    width: 60%
  }
  
  .section-title {
    margin-top: 0;
  }
  
  .section-content {
    list-style-type: none;
    padding: 0;
  }
  
  .footer-section {
    flex-grow: 1;
    margin-right: 30px;
    text-align: left;
    
  }
  .section-title{
   
    line-height: 69px;
  }
  .section-content li {
    margin-bottom: 8px;
    
  }
  
  .section-content li:hover {
    color: #53c549;
    cursor: pointer;
   
  }
  
  .section-title{
    font-size: smaller;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(231, 225, 225);
    font-weight: bold;
    width: 90%
  }
