  }
  .footerlogo{
    width: 30%;
  }

 .footerend{
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: space-between;
  height: 3rem;
  text-align: center;
  border-top: 2px solid rgba(255,255,255,.15);
 }
 .footerend>h5{
  margin-top:.5rem;
  color: azure;
 }
 .footrendright{
  display: flex;
  width: 20%;

justify-content: space-evenly;
margin-right: 4rem;
