}


@media (max-width: 768px) {
  .footer {
    flex-wrap: wrap;
  }

  .footer-section {
    flex-basis: 50%;
    margin-bottom: 30px;
  }

  .footer-section:last-child {
    margin-bottom: 0;
  }
}


@media (max-width: 320px) {
  .footerend {
    display: block;
  }

