.services{
   
height:auto;
background-color: white;
width: 100%;

}
.serviceimg>img{
    width: 50%;
  
  margin-left: 30%;

}
.serviceicons{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 6%; 
   
}

.service_container{
    
    display: flex;
   
    align-items: center;
    
}
.serviceimg{
    margin: 0%;
}
.servicetop{
   
    margin-top: 3%;
}
.downimgad{
    position: relative;
    width:80%;
   margin: auto;
   margin-left: 10%;
  
}





.imgdiv{

width: 50%;
margin: auto;
}
.imagefit{
    display: flex;
    max-width: 100%;
    height: auto;
    object-fit: cover;
margin: auto;
position: relative;
}

.imgdiv{
   
    position: relative;
   /* border: 1px solid green; */
   
}

.imgdiv>Img{
    position: relative;
    border: 1px solid yellow;
}

.textimg{
    position: absolute;
    top: 186%;
    right: 14%;
    /* background-color: aqua; */
    z-index: 2000;
    line-height: 3rem;
    border: 1px solid green;
    /* padding-right: -20rem; */
    
}


.textimg>h4{
    
    color: blueviolet;
    
}

.textimg>h1{
    
    color: black;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
}

.textimg>p{
    
    color: lightblue;
   
    
}

.textimg>p>i{
    
    margin-left: 0.6rem;
   
    
}


.imagefit{
    position: relative;
}
.textimg1{
    position: absolute;
    z-index: 500;
    margin-top: -40%;
    margin-left: 15%;
    line-height:3rem;
}










@media (max-width: 620px)
{

    .imagefit{
        display: block;
       
       
       
    }
    .serviceicons{
        display: block;
    }
    .downimgad{
        width: 100%;
        margin-left: 1%;
      
    }
    .imgdiv{
        width: 90%;
    }

.textimg1{
    top: 75%;
    line-height: 2.5rem;
}
    
    

    
    .textimg1>p{
        
        font-size: smaller;
        color: lightblue;
       
        
    }
    
    .textimg>p>i{
        
        margin-left: 0.6rem;
       
        
    }
}

