@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,200;1,300&family=Open+Sans:wght@400;500&family=Outfit:wght@300&family=Roboto&family=Ubuntu&family=Varela+Round&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@500&display=swap');

@-webkit-keyframes slide-top {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }

    100% {
        -webkit-transform: translateY(-6.25rem);
        transform: translateY(-6.25rem)
    }
}

@keyframes slide-top {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }

    100% {
        -webkit-transform: translateY(-6.25rem);
        transform: translateY(-6.25rem)
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: 'poppins', sans-serif;

    box-sizing: border-box;
}

.head {
    height: 20000000rem;
    display: flex;
    padding: 6rem 0;
    position: fixed;
    padding: 10px;

    z-index: 1400;
    height: 5.5%;
    width: 100%;
    align-items: center;
    background-color: rgb(237, 240, 219);
    justify-content: space-between;
}




.fa-earth-americas {
    color: rgb(255, 51, 0);
}

.fa-map-marker {
    color: rgb(148, 148, 133);
    margin-left: 2%;
    margin-right: 5%;
}

.headleft {

    width: 18%;
    display: flex;
    align-items: center;

    margin-left: 20px;
}

.fa-phone {
    margin-right: 8px;
}

.headright {
    margin-right: 20px;

}

@media (max-width:520px) {
   
    .fa-earth-americas{
        display: block;
    }
    .fa-phone{
        display: none;
    }
.number{
    display: none;
}

}