.toppichead{
    border: 2px solid black;
}
.topbutton{
    display: flex;
    
}
.buttonstyle{
    position: relative;
width: 10rem;
height: 3rem;
margin: 1rem;
color: rgb(0, 146, 102);
border-radius: 10px;
font-size: large;
font-weight: bold;
}

/* .buttonstyle:hover{
    background-color: rgb(27, 147, 222);
} */

.buttonstyle::before{
content: "";
position: relative; 
top: 0;
left: 0;
background-color: rgb(42, 99, 165);
width: 100%;
height: 100%;
transform: translate(-100%);
transition: all 0.3s linear;
visibility: hidden;

}

.buttonstyle:hover:before{
    position: absolute; 
    transform: translate(0);
   visibility: visible;
z-index: -10;
overflow: hidden;

}


.buttonstyle:hover{
    color: antiquewhite;
    z-index: 100;
    overflow: hidden;
    
}








