.carosel{
   
    margin-top: 1.9rem;
}
.carosel{

    margin: auto;
display: flex;    
    

    bottom: 0px;
    height: 70%;
  
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
   
    opacity: 1;
    transition: opacity .3s;
}



div.carousel.ul.thumbs.animated{
     visibility: hidden;
    display: none;
}

.lipstickimg{
    position: relative;
}

.lipstickimgcontent{
    color: white;
    text-align: right;
    position: absolute;
    bottom: 40%;
    right: 10%;
    line-height: 5rem;
    width: 45%;
}

.lipstickimgcontent>p{
    
    font-size: 1.4rem;
    font-weight: 400;
}

.lipstickimgcontent>h1{
    
    font-size: 4rem;
}

.lipstickimgcontent>button{
    
    background-color: #ef476f;
    border-radius: 12px;
    height: 4rem;
    width: 10rem;
    color: white;
    font-size: large;
}

.lipstickimgcontent>button:hover{
    transition: all 0.2s;
    transform: translateY(-20%);
    
}




