@media (max-width: 500px) {


    .jeweleryimg{
        position: relative;
    }
    
    .jeweleryimgcontent{
        margin-top: 60%;
        width: 100%;
        color: white;
        text-align: right;
        position: absolute;
        bottom: -1%;
        margin-right:2%;
        margin-bottom: 9%;
