        line-height: 20px;

    }
    
    .jeweleryimgcontent>p{
        
       font-size: small;
        font-weight: 400;
    }
    
    .jeweleryimgcontent>h1{
        
        font-size: 1rem;
    }
    
    .jeweleryimgcontent>button{
        
