.dealbody {
    background-color: #f5d4e0;

    height: auto;
    display: flex;
    width: 100%;
    height: 100vh;
    /* padding:4rem 0 */

}

.dealleft {

    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
    padding-left: 5%;

    font-size: medium;
    font-family: Arial, Helvetica, sans-serif;
}

.dealleft>h1 {

    padding: 2rem 0;
    font-size: 3rem;
}

.dealleft>p {

    font-size: 1rem;
    padding-bottom: 4%;
    line-height: 140%;
}

.buttonsection {
  


}

.dealleft>.buttonsection>.button1 {
    
    padding: px;
    width: 20%;
    height: 200%;
    margin-right: 6%;
    background-color: #4b93ff;
    border-radius: 4px;
    border: transparent;
    color: white;

    font-family: Arial, Helvetica, sans-serif;
}

.dealleft>.buttonsection>.button2 {
    padding: px;
    width: 20%;
    height: 200%;
    margin-right: 6%;

    border-radius: 4px;
    border: transparent;
    color: white;

    font-family: Arial, Helvetica, sans-serif;
    background-color: #ef476f;

}



.dealright {
    position: relative;
    width: 50%;
    object-fit: cover;

    padding-left: 0%;
}

.dealright>img {
    width: 100%;
    height: 107%;
    object-fit: cover;
    position: absolute;
    padding-left: 0%;
    object-fit: initial;

    margin-top: -7%;
    /* Adjust the margin-top to control the visibility of the upper portion */
    /* Adjust the left margin to control the overflow on the right */

}







@media (max-width: 800px) {

    .dealbody {

        display: block;

    }

    .dealleft>h3 {
        margin-top: 10%;
    }

    .dealleft {

        margin: 1px solid green;
        width: 90%;
        display: flex;
        flex-direction: column;
        text-align: left;
        justify-content: center;
        padding-left: 5%;
        margin-left: 5%;
        margin-right: 5%;
        font-size: medium;
        font-family: Arial, Helvetica, sans-serif;
    }

    .dealright {

        margin-top: 15%;
        width: 90%;
        height: 20rem;
    }

    .dealbody {
        height: auto;
    }

    .dealright>img {

        width: 100%;
        height: 107%;
        object-fit: cover;
        position: absolute;
        padding-left: 0%;
        object-fit: initial;
    }
.dealleft>.buttonsection>.button2 {
    padding: px;
    width: 20%;
    height: 30px;
    margin-right: 6%;

    border-radius: 4px;
    border: transparent;
    color: white;
    margin-top: 5px;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #ef476f;
text-align: center;

}



}