*{
    box-sizing: border-box;
    margin: 0%;
}



.navbar{
    position: fixed;
    position: sticky;
   background-color: rgb(251, 251, 251);
    display: flex;
    height: auto;
    align-items: center;
    justify-content: space-between;
    /* padding2rem; */
    padding: 1rem 4rem;
    top: 5.4%;
  z-index: 1400;
  margin-top: auto;

}

.nav_img>img{

    width: 8rem;

}

.nav_content{
    display: flex;
    position: sticky;
}

.nav_content>ul{
    display: flex;
}

.nav_content>ul>li{
    padding-left: 2rem;
}


.dropdown {
    position: relative;

    
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    border-radius: 8px;
    
    
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    
}

.dropdown-content a:hover {
    background-color: #ddd;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover{
    color: rgb(58, 66, 179);
    cursor: pointer;
}


.dropdown:hover>i{

    transform: rotate(180deg);
    transition: transform 0.4s ease;
    color: rgb(238, 125, 125);
}

.body{
    height: 400rem;
}
.navimg{
    border: 1px solid green;
    width:2.5rem;
    border-radius: 50%;
}
.right_nav{
    display: flex;
    align-items: center;
    padding-left:2rem ;
   padding:0 2rem;

width: 20%;
justify-content: space-between;
}

.navburger{
    display: none;
}

.mob_nav_content{
    display: none;
}

/*responsive section*/
@media (max-width: 797px) {



 /*navbar section*/
.nav_content{
    display: none;
}
.right_nav{
    width: 80%;
    display: flex;
   justify-content: space-around;
   
}
.nav_img{

    display: none;
}
.navbar{
   margin-top: 0%;
    width: 100%;
    justify-content: space-between;
    padding: 4%;
}

.navburger{
    padding: 3%;
    display: block;
    border-radius: 2px;
    background-color: #ddd;
    position: relative;
}

.mob_nav_content{
    border: 1px solid green;
    background-color: bisque;
    position: absolute;
    top:90%;
    left: 0%;
    display: block;
    width: 90%;
    line-height: 2rem;

}

}
  

.fa-cart-shopping{
    color: red;
}